import {IActionModel, ActionType} from '../models/action.model';
import {MethodType} from '../models/interface.model';

const dataSchema = {
    type: "object",
    required: ["key", "type"],
    properties: {
        key: {
            type: "string",
            title: "KEY"
        },
        type: {
            type: "string",
            title: "类型"
        },
        group: {
            type: "string",
            title: "分组名称"
        },
        text: {
            type: "object",
            format: "json",
            title: "SCHEMA配置"
        },
        textForm: {
            type: "array",
            format: "json",
            title: "SCHEMA配置",
            items: {
                type: "object"
            }
        },
        description: {
            type: "string",
            maxLength: 200,
            title: "描述"
        }
    }
};

class List {
    static $inject = ["toolbarUtils", "actionUtils"];
    static key: string = "schemaListAction";

    constructor(toolbarUtils, actionUtils) {
        let actionModel: IActionModel = {
            key: List.key,
            type: ActionType.list,
            title: "SCHEMA管理",
            icon: "view-module",
            list: {
                columns: [
                    actionUtils.columnBuilder("<span>{{::item.id}}</span>", "ID", "id").toValue(),
                    actionUtils.columnBuilder("<span>{{::item.key}}</span>", "KEY").toValue(),
                    actionUtils.columnBuilder("<span>{{ ::item.type }}</span>", "模块类型").toValue(),
                    actionUtils.columnBuilder(`<span>{{ ::item.description }}</span>`, "描述").toValue(),
                ],
                showPagination: true,
                searchActionKey: "",
                showRefreshBtn: true,
                showSearchBtn: true,
                showSearchPanel: false,
                toolbars: [],
                itemToolbars: []
            },
            itemActions: [{key: Edit.key}, {key: Delete.key}, {key: Copy.key}],
            actions: [Add.key],
            interfaces: [{
                key: "modulesList",
                method: MethodType.GET,
                address: "",
                port: null,
                path: "schemas",
                jpp: {
                    set: {
                        "/total": "/count",
                        "/rows": "/rows"
                    },
                    copy: {},
                    move: {},
                    del: {}
                },
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Add {
    static key: string = "schemaAddAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Add.key,
            type: ActionType.form,
            title: "新建SCHEMA",
            icon: "add",
            refreshList: true,
            form: {
                dataSchema: dataSchema,
                formSchema: [{
                    key: "key",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "group",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "type",
                    type: "select",
                    titleMap: [{
                        name: "DATA",
                        value: "DATA"
                    }, {
                        name: "FORM",
                        value: "FORM"
                    }]
                }, {
                    key: "description",
                    type: "textarea",
                    htmlClass: "md-block"
                }, {
                    key: "text",
                    condition: "model.type==='DATA'",
                    type: "jeditor",
                    htmlClass: "md-block"
                }]
            },
            closeDialog: true,
            interfaces: [{
                key: "schemaAdd",
                method: MethodType.POST,
                address: "",
                port: null,
                path: "schemas",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Copy {
    static key: string = "schemaCopyAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Add.key,
            type: ActionType.form,
            title: "复制SCHEMA",
            icon: "copy",
            refreshList: true,
            form: {
                dataSchema: dataSchema,
                formSchema: [{
                    key: "key",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "group",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "type",
                    type: "select",
                    titleMap: [{
                        name: "DATA",
                        value: "DATA"
                    }, {
                        name: "FORM",
                        value: "FORM"
                    }]
                }, {
                    key: "description",
                    type: "textarea",
                    htmlClass: "md-block"
                }, {
                    key: "text",
                    type: "jeditor",
                    htmlClass: "md-block"
                }]
            },
            closeDialog: true,
            interfaces: [{
                key: "schemaCopy",
                method: MethodType.POST,
                address: "",
                port: null,
                path: "schemas",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Edit {
    static key: string = "schemaEditAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Edit.key,
            type: ActionType.form,
            title: "修改SCHEMA",
            icon: "edit",
            refreshList: true,
            form: {
                dataSchema: dataSchema,
                formSchema: [{
                    key: "key",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "group",
                    type: "text",
                    htmlClass: "md-block"
                }, {
                    key: "type",
                    type: "select",
                    titleMap: [{
                        name: "DATA",
                        value: "DATA"
                    }, {
                        name: "FORM",
                        value: "FORM"
                    }]
                }, {
                    key: "description",
                    type: "textarea",
                    htmlClass: "md-block"
                }, {
                    key: "text",
                    condition: "model.type==='DATA'",
                    type: "jeditor",
                    htmlClass: "md-block"
                }, {
                    key: "textForm",
                    condition: "model.type==='FORM'",
                    type: "jeditor",
                    htmlClass: "md-block"
                }]
            },
            closeDialog: true,
            interfaces: [{
                key: "schemaEdit",
                method: MethodType.PUT,
                idFieldPath: "/id",
                address: "",
                port: null,
                path: "schemas",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

class Delete {
    static key: string = "schemaDeleteAction";

    constructor() {
        let actionModel: IActionModel = {
            key: Delete.key,
            type: ActionType.confirm,
            title: "删除SCHEMA",
            icon: "delete",
            refreshList: true,
            confirm: {
                confirmTitle: "",
                confirmContent: "确定要删除SCHEMA吗!"
            },
            interfaces: [{
                key: "schemaDelete",
                method: MethodType.DELETE,
                idFieldPath: "/id",
                address: "",
                port: null,
                path: "schemas",
                isRestful: true
            }]
        };

        return actionModel;
    }
}

export default (module: ng.IModule) => {
    const services: Array<any> = [List, Add, Edit, Delete, Copy];

    _.each(services, (ser)=> {
        module.service(ser.key, ser);
    });
}



